/**
dynamic filtering
*/
form.dynamic-filter input[type=text] {
  border-radius: 10px 0 0 10px;
  padding: 12px;
  font-size: 1.5em;
  border: 1px solid #ccc;
  outline: none;
  float: left;
  width: 80%;
  background: #fbfbfb; }
  form.dynamic-filter input[type=text]:focus {
    box-shadow: 0 0 3px 0 #0a90e4;
    border: 1px solid #0a90e4;
    border-right: none; }
    form.dynamic-filter input[type=text]:focus + button {
      box-shadow: 0 0 3px 0 #0a90e4;
      border: 1px solid #0a90e4;
      outline: none;
      border-left: 0;
      background: #0a90e4; }

form.dynamic-filter button {
  border-radius: 0 10px 10px 0;
  float: left;
  width: 20%;
  padding: 12px;
  background: #138cd8;
  outline: none;
  color: white;
  font-size: 1.5em;
  border: 1px solid #ccc;
  border-left: none;
  cursor: pointer; }
  form.dynamic-filter button:hover {
    background: #0a90e4; }

form.dynamic-filter::after {
  content: "";
  clear: both;
  display: table; }

form.dynamic-filter #filter-settings-switcher {
  display: block;
  margin-top: 10px;
  clear: both;
  padding-top: 5px;
  padding-left: 5px;
  margin-bottom: 5px;
  font-size: 1.1em;
  color: #6f6a6a;
  background: none;
  width: auto;
  border: none; }
  form.dynamic-filter #filter-settings-switcher:hover {
    color: #0e96eb;
    cursor: pointer; }
  form.dynamic-filter #filter-settings-switcher .farrow {
    margin-left: 2px;
    vertical-align: middle; }

form.dynamic-filter #filter-settings {
  border-radius: 10px;
  transition: all 0.5s ease;
  clear: both;
  font-size: 1.4em;
  padding-top: 10px;
  background: #e9e9e9;
  padding: 10px;
  border: 1px solid #ddd; }
  form.dynamic-filter #filter-settings span {
    margin-right: 10px; }
  form.dynamic-filter #filter-settings label {
    font-weight: 500;
    display: block; }
  form.dynamic-filter #filter-settings.slide-up, form.dynamic-filter #filter-settings.slide-down {
    transition: all .3s ease-in-out;
    -webkit-transform-origin: left top;
            transform-origin: left top;
    overflow-y: hidden; }
  form.dynamic-filter #filter-settings.slide-up {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    border-color: transparent;
    padding: 0;
    height: 0; }
  form.dynamic-filter #filter-settings.slide-down {
    -webkit-transform: scaleY(1);
            transform: scaleY(1); }

